export default {
	data() {
		return {
			isLoading: true,
			moneyMsg: null,
			currentItem: 0,
			page: 1,
			isNull_01: false,
			isNull_02: false,
			stopLoad: false,
			salesDetailList: [],
			commissions: [],
			dataLoading: true,
			time: "",
			keyWords: "",
			offset: 10,
			data: null
		}
	},
	onShow() {
		//获取提现/销售额数据
		this.getSalesData()
	},
	onLoad() {
		this.getSalesDetail()
	},
	methods: {
		clearKeyWords() {
			this.keyWords = ""
		},
		searchData() {
			this.isNull_01 = this.isNull_02 = this.stopLoad = false
			this.dataLoading = true
			this.page = 1
			this.time = ''
			switch (this.currentItem) {
				case 0:
					this.getSalesDetail()
					break;
				case 1:
					this.getCommission()
					break;
				default:
					break;
			}
		},
		chooseDate(e) {
			console.log(e.detail.value)
			this.time =  new Date(e.detail.value).getTime()/1000
			console.log(this.time)
			this.isNull_01 = this.isNull_02 = this.stopLoad = false
			this.dataLoading = true
			this.page = 1
			this.keyWords = ''
			switch (this.currentItem) {
				case 0:
					this.getSalesDetail()
					break;
				case 1:
					this.getCommission()
					break;
				default:
					break;
			}
		},
		toHistoryRecode() {
			uni.navigateTo({
				url: '../historyRecode/historyRecode'
			})
		},
		//处理下单时间
		handleTime(time) {
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			let h = date.getHours().toString().padStart(2, '0')
			let m = date.getMinutes().toString().padStart(2, '0')
			let s = date.getSeconds().toString().padStart(2, '0')
			let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s
			return newTime
		},
		//获取累计销售数据和剩余提现金额
		getSalesData() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/userCenter/soldDataTitle",
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.moneyMsg = res.data.data
						this.isLoading = false
						uni.setStorageSync('user_money', this.moneyMsg.user_money)
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		chooseOption(index) {
			this.currentItem = index
			this.isNull_01 = this.isNull_02 = this.stopLoad = false
			this.dataLoading = true
			this.page = 1
			this.keyWords = ''
			this.time = ""
			switch (index) {
				case 0:
					this.getSalesDetail()
					break;
				case 1:
					this.getCommission()
					break;
				default:
					break;
			}
		},
		//跳转提现
		toCashWithdrawal() {
			uni.request({
				url: this.websiteUrl + "/api/GetSystem/index",
				data: {
					token: 'a0z6f0w15dpmawle'
				},
				method: 'POST',
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.is_show == 1) {
							uni.navigateTo({
								url: "../cashWithdrawal/cashWithdrawal"
							})
						} else {
							this.showToast('提现功能已关闭')
						}
					}
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		//获取销售额明细
		getSalesDetail(data) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/salesList",
				data: {
					token: uni.getStorageSync('token'),
					time: this.time,
					keywords: this.keyWords,
					page: this.page,
					offset: this.offset
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.dataLoading = false
						this.salesDetailList = res.data.data.data
						if (this.salesDetailList.length == 0) {
							this.isNull_01 = true
						}
					}
				}
			})
		},
		//获取佣金明细
		getCommission(data) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/userCenter/rebeatList",
				data: {
					token: uni.getStorageSync('token'),
					time: this.time,
					keywords: this.keyWords,
					page: this.page,
					offset: this.offset
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.dataLoading = false
						this.commissions = res.data.data.user_rebate
						if (this.commissions.length == 0) {
							this.isNull_02 = true
						}
					}
				}
			})
		},
		loadMoreSalesdetail() {
			console.log('触底了')
			if (this.stopLoad) {
				return
			}
			this.page++
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/salesList",
				data: {
					token: uni.getStorageSync('token'),
					time: this.time,
					keywords: this.keyWords,
					page: this.page,
					offset: this.offset
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.data.data.length == 0) {
						this.stopLoad = true
						return
					}
					for (let i = 0; i < res.data.data.data.length; i++) {
						this.salesDetailList.push(res.data.data.data[i])
					}
				}
			})
		},
		loadMoreCommissions() {
			console.log('触底了')
			if (this.stopLoad) {
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/userCenter/rebeatList",
				data: {
					token: uni.getStorageSync('token'),
					time: this.time,
					keywords: this.keyWords,
					page: this.page,
					offset: this.offset
				},
				header: this.header,
				success: (res) => {
					// console.log(res.data)
					if (res.data.data.user_rebate.length == 0) {
						this.stopLoad = true
						return
					}
					for (let i = 0; i < res.data.data.user_rebate.length; i++) {
						this.commissions.push(res.data.data.user_rebate[i])
					}
					console.log(this.commissions)
				}
			})
		}
	}
}
